<template>
    <div>
<!--        <b-overlay :show="showLoader" rounded="sm">-->
        <div class="image-loader"  id="image-loader" >
            <lottie-loader :loading="showLoader" />
        </div>
            <div class="row">
                <CustomHeaderAlert :message="message" />
                <div class="col-lg-12">
                    <div class="row">
                        <div class="col">
                            <div class="card">
                                <div class="card-body">
                                    <form @submit.prevent="SearchTransaction">

                                        <div class="row">
                                            <div class="col-lg-3 col-md-3">
                                                <div class="form-group mb-3">
                                                    <label>Select service</label>
                                                    <select class="custom-select" v-model="serviceId" name="serviceId" id="serviceId" v-validate="'required'" >
                                                        <option value="" selected> Select service</option>
                                                        <option :value="serv.getPaypartnerserviceid()" v-for="serv in servicesOptions" :key="serv.getPaypartnerserviceid()">{{ serv.getPaypartnerservicename() }}</option>
                                                    </select>
                                                    <span v-show="vErrors.has('serviceId')" class="help text-danger">{{ vErrors.first('serviceId') }}</span>
                                                </div>
                                            </div>
                                            <div class="col-lg-2 col-md-2">
                                                <div class="form-group mb-3">
                                                    <label ></label>
                                                    <br />
                                                    <button class="btn btn-primary btn-block" id="bkcolor" type="submit" style="margin-top: 8px;" >
                                                        Search
                                                    </button>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
<!--        </b-overlay>-->
        <div class="row">
            <div class="col-12">
                <div class="card">

                    <div class="card-body">
                        <div class="row">
                            <div class="col-2">
                                <h4 class="header-title">Search result</h4>
                            </div>
                            <div class="col-4 text-right">

                            </div>
                        </div>

                        <AlertMessage :variant="dangerSuccess" :message="responseMessage" v-if="responseMessage"/>
                        <p class="text-muted font-13 mb-4"></p>
                        <div class="row mb-md-2">
                            <div class="col-sm-12 col-md-6">
                                <div id="tickets-table_length" class="dataTables_length">
                                    <label class="d-inline-flex align-items-center">
                                        Show&nbsp;
                                        <b-form-select
                                                v-model="perPage"
                                                size="sm"
                                                :options="pageOptions"
                                        ></b-form-select
                                        >&nbsp;entries
                                    </label>
                                    <div v-if="listOfFilteredTransactions.length > 0">
                                        <download-excel :data="this.listOfFilteredTransactions" class="text-success hidefromprint" style="font-weight: bold; cursor: pointer;">
                                            Export to excel
                                        </download-excel>
                                    </div>

                                </div>
                            </div>
                            <!-- Search -->
                            <div class="col-sm-12 col-md-6">
                                <div
                                        id="tickets-table_filter"
                                        class="dataTables_filter text-md-right"
                                >
                                    <label class="d-inline-flex align-items-center">
                                        Search:
                                        <b-form-input
                                                v-model="filter"
                                                type="search"
                                                placeholder="Search..."
                                                class="form-control form-control-sm ml-2"
                                        ></b-form-input>
                                    </label>
                                </div>
                            </div>
                            <!-- End search -->
                        </div>

                        <!-- Table -->
                        <b-overlay :show="showLoaderForTable" rounded="sm">
                            <div class="table-responsive mb-0" id="table">
                                <b-table style="color: #000000 !important;"
                                         :items="listOfPaymentLinks"
                                         :fields="tableFields"
                                         responsive="sm"
                                         :per-page="perPage"
                                         :current-page="currentPage"
                                         :sort-by.sync="sortBy"
                                         :sort-desc.sync="sortDesc"
                                         :filter="filter"
                                         :filter-included-fields="filterOn"
                                         :bordered="true"
                                         :striped="true"
                                         @filtered="onFiltered"
                                >
<!--                                    <template #cell(eganowTranStatus)="data">-->
<!--                                          <span :class="{-->
<!--                                            'text-success p-1': `${data.item.eganowTranStatus}` === 'SUCCESSFUL',-->
<!--                                            'text-danger p-1': `${data.item.eganowTranStatus}` ==='FAILED',-->
<!--                                            'text-primary p-1' : `${data.item.eganowTranStatus}` === 'PENDING'-->
<!--                                          }" class="badge"> {{data.item.eganowTranStatus}}</span>-->
<!--                                    </template>-->

                                    <template #cell(actions)="data">
<!--                                        <a v-b-modal.modal-lg class="text-secondary mr-2" style="cursor: pointer" @click="ViewTransaction(data.item)"  > <i class="mdi mdi-eye"></i> View</a>-->
                                        <a class="btn btn-danger mr-2 text-center" style="cursor: pointer" @click="DeactivateTransaction(data.item)"> <i class="mdi mdi-trash-can"></i> Deactivate</a>
                                    </template>

                                </b-table>
                            </div>
                        </b-overlay>
                        <div class="row">
                            <div class="col">
                                <div
                                        class="dataTables_paginate paging_simple_numbers float-right"
                                >
                                    <ul class="pagination pagination-rounded mb-0">
                                        <!-- pagination -->
                                        <b-pagination
                                                v-model="currentPage"
                                                :total-rows="rows"
                                                :per-page="perPage"
                                        ></b-pagination>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <!-- start View transaction model      -->

        <!--  End  View transaction model     -->
    </div>
</template>

<script>

    import merchantstrans from '../../protos/MerchantMoneyTransferForBusinessPortal_grpc_web_pb'
    import merchantservices from '../../protos/MerchantAccount_grpc_web_pb'
    import grpcUrl from "../../components/constant";
    import CustomHeaderAlert from "../../components/shared/CustomHeaderAlert";
    import SweetAlertConfig from "../../SwalConfig";
    import SweetAlertWrapper from "../../sweetAlert";
    import LottieLoader from "../../components/LottieLoader";
    //import AlertMessage from "../../components/shared/AlertMessage";

    export default {
        components:{

            CustomHeaderAlert,
            LottieLoader
            // AlertMessage
        },
        data(){
            return{
                message :'Account transactions ',
                dangerSuccess : 'danger',
                showLoader : false,
                showLoaderForTable : false,
                responseMessage :'',
                clients:{},
                viewTransaction : {
                    valueDate : '',
                    currencyIso :'',
                    transAmount: '',
                    sourceAccountNo: '',
                    sourceAccountName: '',
                    recipientAccountNo: '',
                    recipientName: '',
                    commissionAmt: '',
                    payPartnerServiceName: '',
                    paypartnerserviceid :'',
                    eganowTranStatus: '',
                    transactionType: '',
                    transTimes: '',
                    transStatusSentToMerchantYesNo:'',
                    gatewayTranStatus: '',
                    transAmountWithCurrency :'',
                    commissionWithCurrency :''

                },
                serviceClients : {},
                listOfPaymentLinks :[],
                listAllpaymentLinksTrans :[],
                listOfFilteredTransactions :[],
                selectedPaymentLink :{},
                transactionType:'',
                serviceId:'',
                startDate:new Date(),
                endDate: new Date(),
                servicesOptions:[],
                totalRows: 1,
                currentPage: 1,
                perPage: 10,
                pageOptions: [10, 25, 50, 100],
                filter: null,
                filterOn: [],
                sortBy: "valueDate",
                sortDesc: false,
                tableFields: [
                    {
                        label:"Narration",
                        key: "Narration",
                        sortable: true,
                        thStyle: { width: "50%" },
                        filter : true

                    },
                    {
                        label:"Date created",
                        key: "MakeDate",
                        sortable: true,
                        filter: true
                    },
                    {
                        label:"Transaction Amount",
                        key: "Amount",
                        sortable: true,
                        filter: true
                    },
                    // {
                    //     label:"Request Status",
                    //     key: "RequestStatus",
                    //     sortable: true,
                    // },

                    {
                        key: "actions",
                        sortable: true,
                    },
                ],
            }
        },
        methods:{
            SearchTransaction(){
                this.$validator.validateAll().then((result) => {
                    if (result) {
                        try {
                            const request = new merchantstrans.PaymentLinkListRequestMessage();
                            this.showLoader = true;
                            document.getElementById("image-loader").style.zIndex = "1";
                            request.setBeneficiarycountrycode(this.$store.getters.getUserInfo.countryCode)
                            request.setBeneficiarypaypartnerserviceid(this.serviceId)
                            request.setLanguageid(this.$store.getters.getUserInfo.languageId)
                               console.log("Request : ", request)

                            this.clients.getPaymentLinkListForMerchant(request,this.$store.getters.metadata, (error, res) => {
                                this.showLoader = false;
                                document.getElementById("image-loader").style.zIndex = "0";
                                if(error){
                                    console.log(" error : ", error)
                                    this.responseMessage = grpcUrl.serverError
                                }

                                if(res){

                                    this.listOfPaymentLinks = []
                                    this.listAllpaymentLinksTrans =  res.getLinklistList();
                                    // console.log(" Transaction list : ", res.getTranslistList())
                                    res.getLinklistList().forEach((data) => {
                                        this.listOfPaymentLinks.push({
                                            TransRefNo : data.getPaymentbylinktransrefno(),
                                           // ServiceId : data.getBeneficiarypaypartnerserviceid(),
                                           //  BCurrency : data.getBeneficiarytranscurrecyiso(),
                                            Amount : grpcUrl.formatMoney(data.getTransamount(),data.getBeneficiarytranscurrecyiso()),
                                            // CanUserChangeAmount : data.getCanuserchangeamountyesno(),
                                            // BCountryCode : data.getBeneficiarycountrycode(),
                                           // RequestStatus : data.getRequeststatus(),
                                            Narration : data.getTransnarration(),
                                            MakeDate : data.getMaketimestr(),
                                           // PaymentlinkType : data.getPaymenlinktype(),
                                            // NoOfdaysToExpire : data.getNoofdaystoexpire(),
                                            // LinkExpirydate : data.getLinkexpirydate(),
                                            // PaymentLinkEndpoint : data.getPaymentlinkendpoint(),
                                            // LanguageId : data.getLanguageid()
                                        })
                                    })
                                    this.listOfFilteredTransactions = this.listOfPaymentLinks
                                }
                            })
                            // GetMerchantServiceAccountTrans
                        }catch (e) {
                            this.responseMessage = grpcUrl.serverError
                            console.log(" Error : ", e)
                        }
                        return;
                    }
                });

            },
            DeactivateTransaction(data){
                try {

                   this.selectedPaymentLink =  this.listAllpaymentLinksTrans.filter((d) => d.getPaymentbylinktransrefno() === data.TransRefNo)
                    this.sweetAlert.showDialogYesOrNo(Object.assign(SweetAlertConfig, {
                        title :'Deactivate payment link ?',
                        confirmButtonText : 'Yes, deactivate'
                    }), () => {

                        let request = new merchantstrans.PaymentLinkRefNoDataRequestMessage()

                        request.setLanguageid(this.$store.getters.getUserInfo.languageId)
                        request.setBeneficiarycountrycode(this.selectedPaymentLink[0].getBeneficiarycountrycode())
                        request.setPaymentbylinktransrefno(this.selectedPaymentLink[0].getPaymentbylinktransrefno())
                        request.setBeneficiarypaypartnerserviceid(this.selectedPaymentLink[0].getBeneficiarypaypartnerserviceid())
                       // console.log(" Request data : ", request)
                        this.showLoaderForTable = true
                        this.clients.deactivatePaymentLink(request, this.$store.getters.metadata, (err,res) => {
                            this.showLoaderForTable = false
                            if(err) {
                                console.log(" Error : ", err)
                            }
                            if(res.getIssuccess()) {
                                this.SearchTransaction()
                                this.sweetAlert.showMessage("Deactivate payment link ", res.getMessage())

                            }
                            else{

                                this.responseMessage = res.getMessage()
                            }

                        })

                    })
                }catch (e) {
                    console.log(" Error : ", e)
                }
            },
            GetMerchantSevicesByLangIdCountryCode(){
                try {
                    const request = new merchantservices.PayPartnerMerchantCountryMessage();
                    request.setLanguageid(this.$store.getters.getUserInfo.languageId) // en
                    request.setCountrycode(this.$store.getters.getUserInfo.countryCode)

                    this.serviceClients.getMerchantServices(request, this.$store.getters.metadata, (err, res) => {
                        if(err){
                            console.log("Error : ", err)
                        }
                        if(res) {
                            this.servicesOptions = res.getServicelistList()
                          //  console.log(" Service info : ", res.getServicelistList())
                        }
                    })

                }catch (e) {
                    console.log(e.message)
                }
            },
            //NOT IN USE
            ViewTransaction(data){
                try {
                    console.log(" Journal ref no : ", data.journalreferrenceno)
                    this.viewTransaction.sourceAccountName = data.sourceAccountName
                    this.viewTransaction.sourceAccountNo = data.sourceAccountNo
                    this.viewTransaction.currencyIso = data.currencyISO
                    this.viewTransaction.commissionAmt = data.commissionAmt
                    this.viewTransaction.eganowTranStatus = data.eganowTranStatus
                    this.viewTransaction.gatewayTranStatus = data.gatewayTranStatus
                    this.viewTransaction.paypartnerserviceid = data.paypartnerserviceid
                    this.viewTransaction.payPartnerServiceName = data.payPartnerServiceName
                    this.viewTransaction.recipientAccountNo = data.recipientAccountNo
                    this.viewTransaction.recipientName = data.recipientName
                    this.viewTransaction.transTimes = data.transTimes
                    this.viewTransaction.transactionType = data.transactionType
                    this.viewTransaction.transAmountWithCurrency =  data.transAmount
                    this.viewTransaction.commissionWithCurrency = data.currencyISO +" "+ data.commissionAmt
                    this.viewTransaction.valueDate = data.valueDate
                } catch (e) {
                    console.log("Error : ", e)
                }

            },
            onFiltered(filteredItems) {
                //this.listOfFilteredAccounttransactions = []
                // console.log(" filter using filter : ", this.filter)
                // console.log(" filter data : ", filteredItems)
                this.listOfFilteredTransactions = filteredItems
                // Trigger pagination to update the number of buttons/pages due to filtering
                this.totalRows = filteredItems.length;
                this.currentPage = 1;
            },
        },
        computed:{
            rows() {
                return this.listOfFilteredTransactions.length;
            },
        },
        mounted() {
            //CustomerLastNTransRequestMessage

        },
        created() {
            this.clients = new merchantstrans.MerchantMoneyTransferForBusinessPortalSvcClient(grpcUrl.URL,null, null)
            this.serviceClients = new merchantservices.MerchantAccountSvcClient(grpcUrl.URL,null,null)
            this.sweetAlert = new SweetAlertWrapper()
            this.GetMerchantSevicesByLangIdCountryCode()
            this.$store.dispatch('setAppHeader', {
                title: "Account Transaction",
                items: [
                    {
                        text: "Dashboard",
                        href: "#",
                    },
                    {
                        text: "Account Transaction",
                        href: "/business/accounttransaction",
                    },
                ],
            })
        }

    }
</script>

<style scoped>

</style>